"use client";

import {
  keepPreviousData,
  useMutation,
  useQuery,
  useQueryClient,
} from "@tanstack/react-query";
import React, { useState } from "react";
import { useSearchParams, useRouter } from "next/navigation";
import { CheckSquare, X } from "@deemlol/next-icons";
import DeleteIcon from "@mui/icons-material/Delete";
import DoneIcon from "@mui/icons-material/Done";
import ReceiptLongIcon from "@mui/icons-material/ReceiptLong";
import Pagination from "@mui/material/Pagination";

import {
  postTodo,
  deteleTodo,
  updateTodoStatus,
  getTodos,
} from "../services/api";
import { Todo } from "app/type/type";
import AddTodo from "app/components/addTodo";
import FilterTodo from "app/components/filterTodo";
import Statistic from "app/components/statisticTodo";
import Grid from "@mui/material/Grid";
import Card from "@mui/material/Card";
import Typography from "@mui/material/Typography";
import CardContent from "@mui/material/CardContent";
import CardActions from "@mui/material/CardActions";
import Button from "@mui/material/Button";
import MuiPagination from "./pagination";

interface TodoResponse {
  todos: Todo[];
  total: number;
  page: number;
  limit: number;
  completedCount: number;
  unCompletedCount: number;
}

export default function TodoPage1() {
  const searchParams = useSearchParams();
  const router = useRouter();

  // get params
  const inputSearch = searchParams.get("kw") || "";
  const selectStatus = searchParams.get("status") || "";
  const currentPage = Number(searchParams.get("page")) || 1;
  const sortOrder = (searchParams.get("order") as "asc" | "desc") || "asc";
  const sortField =
    (searchParams.get("sortField") as "todo" | "createdDate" | "status") ||
    "status";

  // set state
  const [searchInput, setSearchInput] = useState(inputSearch);
  const [input, setInput] = useState("");
  const [editId, setEditId] = useState<number | null>(null);
  const [editTodo, setEditTodo] = useState("");

  // pageSize
  const pageSize = 10;

  const queryClient = useQueryClient();

  // search keyword
  const handleSearch = (value: string) => {
    const params = new URLSearchParams(searchParams.toString());
    params.set("kw", value);
    params.set("page", "1");
    router.push(`?${params.toString()}`);
  };

  // Get all Todo or get Todo by keyword or get Todo by status
  const { data, isLoading } = useQuery<TodoResponse, Error>({
    queryKey: [
      "todos",
      inputSearch,
      selectStatus,
      sortOrder,
      sortField,
      currentPage,
      pageSize,
    ],
    queryFn: async () => {
      const result = await getTodos({
        kw: inputSearch,
        status: selectStatus,
        order: sortOrder,
        sortField: sortField,
        currentPage: currentPage,
        pageSize: pageSize,
      });
      console.log("QueryFn result:", result);
      return result;
    },
    staleTime: 30 * 1000,
    gcTime: 5 * 1000 * 60,
    placeholderData: keepPreviousData,
  });

  // Add todo
  const postTodoMutation = useMutation({
    mutationFn: postTodo,
    onSuccess: () => {
      queryClient.invalidateQueries({
        queryKey: [
          "todos",
          inputSearch,
          selectStatus,
          sortOrder,
          sortField,
          currentPage,
          pageSize,
        ],
      });
    },
  });

  // delete todo
  const deleteTodoMutation = useMutation({
    mutationFn: deteleTodo,
    onSuccess: () => {
      queryClient.invalidateQueries({
        queryKey: [
          "todos",
          inputSearch,
          selectStatus,
          sortOrder,
          sortField,
          currentPage,
          pageSize,
        ],
      });
      setEditTodo("");
    },
  });

  // update todo
  const updateTodoMutation = useMutation({
    mutationFn: updateTodoStatus,
    onSuccess: () => {
      queryClient.invalidateQueries({
        queryKey: [
          "todos",
          inputSearch,
          selectStatus,
          sortOrder,
          sortField,
          currentPage,
          pageSize,
        ],
      });
    },
  });

  // Get Data Todo
  let todos: Todo[] = Array.isArray(data?.todos)
    ? data.todos.map((todo: Todo) => ({
        ...todo,
        createdDate: todo.createdDate,
      }))
    : [];

  // Rendering
  return (
    <div className="text-2x1 text-white bg-[#FFE794] h-full grid-cols-subgrid border-2">
      {/* Header */}
      <div className="basis-128 text-center mb-4 mr-50 ml-50 text-5xl bg-[#FFE794] text-[#050505] font-extrabold">
        <ReceiptLongIcon />
        YourTODO
      </div>
      {/* Input và filter */}
      <div className="flex items-center gap-4 border bg-[#FFD7A7] mb-6 p-4 rounded-xl shadow ml-50 w-278 text-black">
        {/* Add todo */}
        <AddTodo
          input={input}
          setInput={setInput}
          onAdd={() => {
            if (input.trim()) {
              postTodoMutation.mutate({
                todo: input,
              });
              setInput("");
            }
          }}
        />
        {/* Search & Filter Status*/}
        <FilterTodo
          searchInput={searchInput}
          setSearchInput={setSearchInput}
          handleSearch={handleSearch}
          selectStatus={selectStatus}
          searchParams={searchParams}
          router={router}
        />
      </div>
      {/* Statistic */}
      {data && (
        <Statistic
          total={data.total || 0}
          completedCount={data.completedCount || 0}
          unCompletedCount={data.unCompletedCount || 0}
        />
      )}
      {isLoading ? (
        <tr>
          <td colSpan={3} className="text-center">
            Đang tải...
          </td>
        </tr>
      ) : todos.length === 0 ? (
        <tr>
          <td colSpan={3} className="text-center">
            Không có kết quả
          </td>
        </tr>
      ) : (
        <div className="ml-20">
          <Grid container spacing={4} columns={{ xs: 4, sm: 8, md: 12 }}>
            {todos.map((todo) => (
              <Grid size={3} key={todo.id} justifyContent={"space-between"}>
                <Card sx={{ width: 350 }}>
                  <CardContent>
                    <Typography align="center" variant="h5">
                      {todo.todo}
                    </Typography>
                    <Typography variant="h6">
                      {todo.completed ? (
                        <>
                          <CheckSquare className="text-green-600 inline" /> Done
                        </>
                      ) : (
                        <>
                          <X className="text-red-600 inline" /> Pending
                        </>
                      )}
                    </Typography>
                    <Typography>
                      {todo.createdDate
                        ? new Date(todo.createdDate).toLocaleDateString("vi-VN")
                        : ""}
                    </Typography>
                  </CardContent>
                  <CardActions>
                    <Button
                      onClick={() =>
                        updateTodoMutation.mutate({
                          id: todo.id,
                          completed: !todo.completed,
                        })
                      }
                    >
                      <DoneIcon />
                      Mark Done
                    </Button>
                    <Button
                      color="error"
                      onClick={() => deleteTodoMutation.mutate({ id: todo.id })}
                    >
                      <DeleteIcon />
                      Delete
                    </Button>
                  </CardActions>
                </Card>
              </Grid>
            ))}
          </Grid>
        </div>
      )}
      {data && (
        <MuiPagination
          currentPage={currentPage}
          total={data.total}
          pageSize={pageSize}
          router={router}
          searchParams={searchParams}
        />
      )}
    </div>
  );
}
